import React from 'react';
import { Helmet } from 'react-helmet'

import Hero from '../components/Hero';
import Content from '../components/Content';
import me from '../assets/images/me.jpg';
import resume from '../assets/documents/Resume.pdf';

function ExperiencePage(props) {
  return (
    <div>
      <Helmet><title>{props.title} | Brendan Benner</title></Helmet>
      <Hero title={props.title}/>

      <Content>
        <br />
        <h2>Chisel HDL GUI Intern</h2>
        <h5><a href="https://intensivate.com/" target="_blank"> Intensivate</a></h5>
        <p><i>Technologies:</i> JavaScript, Chisel, Data Structures, Object Oriented Programming</p>
        <p>This summer, I worked with Intensivate, a company that specializes in creating performative accelerators, to develop a graphical user interface that will be used to design Intensivate’s commercial chips. Using my knowledge of hardware design and low-level systems, I developed a large-scale application in JavaScript used to link Chisel hardware-description code to visual representations of UC Berkeley’s Rocket Chip. This project gave me valuable experience in software development with complex data structures and reinforced my understanding of hardware and machine structures along with my object-oriented programming skills.</p>
        <br />
        <h2>Web Engineer Intern</h2>
        <h5><a href="https://risenetwork.net/" target="_blank"> The Rise Network</a></h5>
        <p><i>Technologies:</i> JavaScript, ReactJS, MongoDB, and HTML/CSS</p>
        <p>Worked with executives to integrate design elements into front and back-end web development plans in a collaborative environment, including a location API for recommending local businesses to users</p>
        <br />
        <h2>EECS 16B (Designing Information Devices and Systems II) Tutor</h2>
        <h5><a href="https://www.eecs16b.org/" target="_blank"> EECS 16B, UC Berkeley</a></h5>
        <p>Taught circuitry and linear algebra concepts to students taking Berkeley's most-taken EE/CS upper-division course; aided students with conceptual difficulties in class and office hours for six hours per week.</p>
        <br />
        <center><a href={resume} target="_blank" className="display-4 font-weight-light nounderline">Resume</a></center>
      </Content>
    </div>
  );
}

export default ExperiencePage;
