import React from 'react';
import {BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import './App.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import ExperiencePage from './pages/ExperiencePage';
import ContactPage from './pages/ContactPage';
import ProjectsPage from './pages/ProjectsPage';

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: "Brendan Benner",
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'Experience', path: '/experience' },
        { title: 'Contact', path: '/contact' },
        { title: 'Projects', path: '/projects' }
      ],
      home: {
        title: 'Brendan Benner',
        subTitle: 'Computer Science Student @ UC Berkeley',
        text: 'Aspiring Software Engineer'
      },
      experience: {
        title: 'Experience'
      },
      contact: {
        title: 'Contact Me'
      },
      projects: {
        title: 'Projects',
        subTitle: 'Checkout my projects below',
      }
    }
  }

  render() {
    return (
      <Router>
        <Container className="p-0" fluid={true}>

          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>Brendan Benner</Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/experience">Experience</Link>
                {/*
                <Link className="nav-link" to="/contact">Contact</Link>
                */}
                <Link className="nav-link" to="/projects">Projects</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>

          <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
          <Route path="/experience" render={() => <ExperiencePage title={this.state.experience.title} />} />
          {/*
          <Route path="/contact" render={() => <ContactPage title={this.state.contact.title} />} />
          */}
          <Route path="/projects" render={() => <ProjectsPage title={this.state.projects.title} subTitle={this.state.projects.subTitle} />} />

          <Footer />

        </Container>
      </Router>
    );
  }
}

export default App;
