import React from 'react';
import { Helmet } from 'react-helmet'

import Hero from '../components/Hero';
import Content from '../components/Content';
import me from '../assets/images/me.jpg';
import resume from '../assets/documents/Resume.pdf';

function HomePage(props) {
  return (
    <div>
      <Helmet><title>{props.title} | UC Berkeley</title></Helmet>
      <Hero title={props.title} subTitle={props.subTitle} text={props.text} />

      <Content>
        {/*<center><img src={me} className="me" alt="Me" /></center>*/}
        <p>I'm a third-year undergraduate at University of California, Berkeley seeking a Bachelor of Arts in Computer Science and a minor in Mathematics. I am interested in systems programming and computer architecture, and I have experience developing with C, Java, Python, Javascript, Scheme, SQL, HTML, and CSS.</p>

        <p>This summer, I worked with Intensivate, a company that specializes in creating performative accelerators, to develop a graphical user interface that will be used to design Intensivate’s commercial chips. Using my knowledge of hardware design and low-level systems, I developed a large-scale application in JavaScript used to link Chisel hardware-description code to visual representations of UC Berkeley’s Rocket Chip. This project gave me valuable experience in software development with complex data structures and reinforced my understanding of hardware and machine structures along with my object-oriented programming skills.</p>

        <p>I am also a motivated and diligent person who can work both by myself and with others. I am open to intern and research positions and am eager to put my skills to use. Please do not hesitate to contact me with any questions using my contact information.</p>
        <br />
        <center><a href={resume} target="_blank" className="display-5 font-weight-light nounderline">Resume</a></center>
        <br />
        <h3>Contact Information</h3>
        <a href="tel:8182599942">Phone: (818) 259-9942</a>
        <br />
        <a href="mailto: brendanrbenner@gmail.com">Email: brendanrbenner@gmail.com</a>
        <br />
        <a href="https://github.com/bbenner7635">GitHub</a>
      </Content>
    </div>
  );
}

export default HomePage;
